
.contact {

    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 36px;
    padding-bottom: 5rem;


}

.title h2 {
    color: #222;
    text-align: center;
    font-size: 23px;
    font-weight: 800;
    padding-top: 5rem;

}

.contact-form {
    grid-template-columns: repeat(minmax(500px), 1fr);
    padding-top: 50px;
}

.content {
    align-self: start;
}

.content p {
    font-size: 16px;
    color: #222;

    
    

}

.content h3 {
    color: #222;
    font-size: 18px;
    font-weight: 700;
}



.content span {
    color: #222;
}

  

.contact-wrapper {
    display: grid;
    grid-template-columns: 60px 200px;
    grid-template-rows: repeat(1, 1fr);
    margin-bottom: 15px;
}

.contact-wrapper > *:first-child {
    grid-row: 1 / span 2;
    align-self: center;
    justify-self: start;
}

.contact-wrapper span {

    font-size: 18px;
    color: #222;
    font-weight: 500;
}

.contact-wrapper h3,
.contact-wrapper a {
    color: #222;
    font-size: 16px;
    font-weight: 500;
}
.border {
    border-bottom: 2px solid #000; /* Adjust the border properties as needed */
    max-width: 200px; /* Set a maximum width for the border */
    width: 100%; /* Ensure the border takes up the full width within the container */
    margin: 0 auto; /* Center the border within the container */
    margin-bottom: 20px; /* Add margin to create space between the border and the cards */
  }
.heading h3 {
    color: #222;

    font-size: 18px;
    margin-top: -5px;
}

@keyframes phoneVibrate {
    0%, 100% {
      transform: translateX(0);
    }
    25%, 75% {
      transform: translateX(-1.5px);
    }
    50% {
      transform: translateX(3px);
    }
  }
  
  

/* Media Queries */

/* Küçük ekranlar için medya sorgusu */
@media screen and (max-width: 767px) {
    .contact-form {
        max-width: 100%;
    }
}

/* Orta ekranlar için medya sorgusu */
@media screen and (min-width: 768px) and (max-width: 1023px) {
    .contact-form {
        max-width: 768px;
        margin: 0 auto; /* Formu ortalamak için margin eklendi */
    }
}

/* Daha büyük ekranlar için medya sorgusu */
@media screen and (min-width: 1024px) {
    .contact-form {
        max-width: 1024px;
        margin: 0 auto; /* Formu ortalamak için margin eklendi */
    }
}