.aboutSection {
    padding: 5rem 0;
    background-color: var(--bg-light2);
  }
  
  .about {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
  }
  
  .aboutImg img {
    border-radius: 50%;
    width: 200px;
    height: 200px;
    border: 4px solid #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .aboutContent {
    max-width: 600px;
    text-align: left;
    padding: 20px;
  }
  
  .aboutHeader {
    margin-bottom: 2.5rem;
  text-align: center;
    font-size: 23px;
    color: #222;
  }
  
  .aboutText {
    font-size: 16px;
    color: #555;
    line-height: 1.6;
  }
  
  .name {
    font-weight: bold;
    color: rgba(52, 152, 219, 0.9)
  }
  
  .border {
    border-bottom: 2px solid #3498db;
    padding-bottom: 10px;
    margin-bottom: 20px;
  }
  
  /* Medya sorguları ile responsive tasarım */
@media screen and (max-width: 768px) {
  .about {
      flex-direction: column;
      text-align: center;
  }

  .aboutContent {
      text-align: center;
  }
}

@media screen and (min-width: 769px) and (max-width: 1200px) {
  .aboutContent {
      max-width: 480px;
  }
}

/* Büyük ekranlar için */
@media screen and (min-width: 1201px) {
  .aboutContent {
      max-width: 600px;
  }
}

/* ... Diğer stiller ... */

/* Daha büyük ekranlar için */
@media screen and (min-width: 1601px) {
  .aboutContent {
      max-width: 800px;
  }
}

