.home {
    display: flex;
    justify-content: space-around;
    align-items: center;
    overflow: hidden;
    line-height: 1;
    height: calc(90vh);
}

.home-content {
    margin-bottom: 100px;
}

.home-title {
    align-items: start;
}

.home-title h2 {
    color: #222;
    font-weight: 700px;
    padding-bottom: 1rem;
    font-size: 1.2rem;
    transform: translateY(500px);
    animation: slideUp 0.8s ease forwards 0.5s;
}

.text-wrapper {
    color: #222;
    display: flex;
    align-items: center;
    font-size: 1rem;
    transform: translateY(500px);
    animation: slideUp 0.8s ease forwards 0.5s;
}

.text-wrapper p {
    color: #222;
    font-size: 3rem;
    font-weight: 600;
    
}


