.form-control > :nth-child(odd) {
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 500;
    font-size: 16px; /* Daha büyük yazı boyutu */
    border-radius: 12px; /* Daha yuvarlatılmış köşeler */
    line-height: 18px; /* Artan çizgi yüksekliği */
    border: 2px solid #ddd;
    padding: 12px; /* Daha büyük iç boşluklar */
    margin-top: 12px; /* Artan üst boşluk */
    margin-bottom: 12px; /* Daha az alt boşluk */
    width: 500px;
    outline: none;
    box-sizing: border-box;
    transition: all 0.3s;
}

.form-control > :nth-child(odd)::placeholder {
    color: #aaa;
}



.textarea {
    height: 250px; /* Daha yüksek textarea yüksekliği */
    line-height: 250px; /* Daha büyük textarea çizgi yüksekliği */
    resize: vertical;
}

.button {
    font-family: 'Josefin Sans', sans-serif;
    width: 150px; /* Daha geniş buton */
    height: 40px; /* Daha yüksek buton */
    background-color:  #333; /* Daha koyu arka plan renk */
    color: #fff;
    border-radius: 12px;
    border: 0;
    cursor: pointer;
    font-size: 16px; /* Daha büyük yazı boyutu */
    padding: 10px;
    transition: all 0.3s;
}

.button:hover {
    background-color: #111; /* Daha koyu hover rengi */
}

.invalid input,
.invalid textarea {
    border: 2px solid #ff5555 !important;
    background-color: #ffdddd;
}

.invalid input:focus,
.invalid textarea:focus {
    border-color: #ff0000;
    background-color: #ffc0c0;
}

.error {
    color: #ff5555;
    font-size: 14px;
}

@media screen and (max-width: 600px) {
    .button {
        width: 100%;
    }
}
